<template>
    <div class="tagging-editor">
        <navigate :title="title" position="absolute"/>
        <div class="scroll-wrapper">
            <mu-form class="form" :model="form" label-position="left">
                <mu-form-item class="form-item border-1px-b" label="标注名称" prop="title">
                    <mu-text-field v-model="form.title" placeholder="请输入标注名称 ..." solo/>
                </mu-form-item>
                <mu-form-item class="form-item border-1px-b" label="类型" prop="type"
                              @click.native="open = true">
                    <div class="form-item-types">
                        <span v-if="form.type">{{type(form.type).name}}</span>
                        <span v-else>请选择</span>
                    </div>
                </mu-form-item>
                <mu-form-item class="form-item" label="共享" prop="shared" v-if="mode === 'tagging'">
                    <div class="form-item-types">
                        <mu-switch v-model="form.shared"/>
                    </div>
                </mu-form-item>
                <template v-if="mode === 'report'">
                    <div style="height: 20px;background-color: #efefef;"></div>
                    <mu-form-item class="picture" prop="image" label-position="top">
                        <div class="picture-label border-1px-b" slot="label">照片</div>
                        <div class="picture-wrapper">
                            <div class="picture border-1px" v-if="!form.image" @click="handlerAppend">
                                <mu-icon class="append" value="add"/>
                            </div>
                            <div class="picture" :style="{backgroundImage: `url('${form.image}')`}" v-else>
                                <mu-button color="#f44336" @click.stop="deletePicture" icon small>
                                    <mu-icon value="delete"/>
                                </mu-button>
                            </div>
                        </div>
                    </mu-form-item>
                </template>
                <div style="height: 20px;background-color: #efefef;"></div>
                <mu-form-item class="abstract" prop="detail" label-position="top">
                    <div class="abstract-label border-1px-b" slot="label">描述</div>
                    <mu-text-field v-model="form.detail" class="abstract-input border-1px-b"
                                   placeholder="请输入描述"
                                   :rows="8"
                                   :max-length="300"
                                   full-width
                                   multi-line
                                   solo/>
                </mu-form-item>
            </mu-form>
            <div class="form-item-button">
                <mu-button class="button" color="#fff" data-mu-loading-size="24"
                           v-loading="loading"
                           @click="handlerSubmit"
                           :disabled="loading"
                           flat>提交
                </mu-button>
            </div>
        </div>

        <mu-bottom-sheet :open.sync="open" lock-scroll>
            <mu-list @item-click="open = false">
                <mu-list-item @click="handlerToggleType(item.id)" v-for="(item, index) in types" :key="index" button>
                    <mu-list-item-action>
                        <mu-icon value="room" :color="form.type === item.id ? 'orange' : ''"></mu-icon>
                    </mu-list-item-action>
                    <mu-list-item-title>{{item.name}}</mu-list-item-title>
                </mu-list-item>
            </mu-list>
        </mu-bottom-sheet>

        <!-- file -->
        <div style=" position: absolute; top: -10px; right: -10px; width: 0; height: 0; overflow: hidden;">
            <input type="file" @change="appendImages" ref="file">
        </div>
        <!-- file -->
    </div>
</template>

<script>
    import Navigate from "../../../base/navigate/navigate"
    import {formatDate} from '../../../common/utils/time'
    import {saveOrUpdateILabel, queryILabelDetail} from "../../../api/personal"
    import {queryTypeDictByFid} from "../../../api/search"
    import {blobToBase64} from "../../../types"

    const name = 'SELECTION_and_TAGGING_and_REPORT_EDITOR'

    export default {
        name,
        data() {
            return {
                open: false,
                show: true,
                hide: false,
                loading: false,
                taggingTypes: [],
                reportTypes: [],
                form: {
                    title: "",
                    type: "",
                    shared: false,
                    image: '',
                    detail: "",
                    longitude: "",
                    latitude: ""
                }
            }
        },
        props: {
            id: {
                type: String,
                default: ''
            },
            longitude: {
                type: [String, Number],
                default: 0
            },
            latitude: {
                type: [String, Number],
                default: 0
            }
        },
        computed: {
            title() {
                if (this.$route.name === 'selection-point') {
                    return '添加标注'
                }

                if (this.$route.name === 'about-tagging-editor') {
                    return '修改标注'
                }

                if (this.$route.name === 'selection-report') {
                    return '危险上报'
                }

                return ''
            },
            mode() {
                if (this.$route.name === 'selection-point') {
                    return 'tagging'
                }

                if (this.$route.name === 'about-tagging-editor') {
                    return 'tagging'
                }

                if (this.$route.name === 'selection-report') {
                    return 'report'
                }

                return ''
            },
            types() {
                if (this.$route.name === 'about-tagging-editor') {
                    return this.taggingTypes
                }

                if (this.$route.name === 'selection-point') {
                    return this.taggingTypes
                }

                if (this.$route.name === 'selection-report') {
                    return []
                }

                return []
            }
        },
        created() {
            queryTypeDictByFid('001').then(res => {
                this.taggingTypes = res
            })
        },
        activated() {
            this.form = {
                title: '',
                type: '',
                shared: false,
                image: '',
                detail: '',
                longitude: '',
                latitude: ''
            }

            if (this.mode === 'tagging') {
                this.id && queryILabelDetail(this.id).then(res => {
                    this.form = res
                })
            }

            if (this.mode === 'report') {

            }
        },
        methods: {
            handlerAppend() {
                this.$refs.file && this.$refs.file.click()
            },
            /***
             * 添加图片
             */
            async appendImages(event) {
                this.form.image = await blobToBase64(event.target.files[0])

                event.target.value = null
            },
            /***
             * 删除图片
             */
            deletePicture() {
                this.form.image = ''
            },
            // 切换类型
            handlerToggleType(typeId) {
                this.form.type = typeId
            },
            // 类型
            type(value) {
                console.log("value", value)
                const result = this.types.find(item => {
                    return item.id === value
                })
                return result || {}
            },
            // 提交
            handlerSubmit() {
                this.loading = true

                this.$toast({
                    type: 'loading',
                    position: 'center',
                    message: '正在上传 ...',
                    duration: 0
                })

                const data = {
                    title: this.form.title,
                    type: this.form.type,
                    shared: this.form.shared,
                    detail: this.form.detail,
                    createTime: formatDate(Date.now(), 'YYYY-MM-DD HH:mm:ss'),
                    longitude: this.form.longitude || this.longitude,
                    latitude: this.form.latitude || this.latitude
                }

                if (this.id) {
                    data.id = this.id
                }
                if (!data.type) {
                    this.loading = false
                    this.$toast({
                        message: '请选择标注类型'
                    })
                    return
                }

                if (data.title == "") {
                    this.loading = false
                    return this.$toast({message: '请输入标注名称'})
                }
                saveOrUpdateILabel(data).then(res => {
                    this.loading = false
                    this.$emit('update')
                    if (this.id) {
                        this.$toast({
                            message: '修改成功'
                        })

                        this.$router.back()
                    } else {
                        this.$toast({
                            message: '添加成功'
                        })

                        window.Arcgis && window.Arcgis.refreshMapLayers()

                        this.$router.replace('/')
                    }
                }).catch(error => {
                    this.$toast('添加失败')

                    this.loading = false
                })
            }
        },
        components: {
            Navigate
        },
        filters: {
            formatDate: formatDate
        },
    }
</script>

<style lang="stylus" scoped>
    @import "../../../common/stylus/variable.styl"

    .tagging-editor
        position absolute
        top 0
        left 0
        right 0
        bottom 0
        padding-top 46px
        background-color #fff
        .scroll-wrapper
            width 100%
            height 100%
            .form
                display flex
                flex-direction column
                padding-top 8px
                .form-item
                    padding-left 15px
                    padding-right 15px
                .form-item-types
                    display flex
                    align-items center
                    justify-content flex-end
                    width 100%
                    height 100%
                    span
                        color #5B8CC0
                        font-size $font-size-sl
                .abstract
                    .abstract-label
                        padding 10px 15px
                    .abstract-input
                        padding 10px 15px 30px
                .picture
                    .picture-label
                        padding 10px 15px
            .form-item-button
                padding 10px
                .button
                    width 100%
                    background-color #3899D7
                    border-radius 5px
</style>



